.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body{
}

.instruction-list {
  margin-top: 10pt;
}

.margin5 {
  margin: 5pt;
}

.instructions {
  position: absolute;
  width: 85%;
  height: 85%;

}

.game-title {
  margin-bottom: 10pt;
}

.game-info-button {
  margin-top: 10pt;
}

.inside-paper-container {
  padding: 10pt;
}

.instructions-paper {
  max-height: 100%;
  overflow: scroll;
}

.player-chip {
  margin: 6pt;
}

.margin10 {
  margin: 10pt;
}

.bottom-nav {
  background-color: white;
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 0px;
  height: 40pt;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-top-width: 1pt;
  border-top-color: lightgrey;
  border-top-style: solid;
}

.player-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.whole-window {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
 
}

.waiting-room-container {

}

.margin15 {
  margin: 15pt;
}

.start-options {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.s-o-b {
  margin: 15pt;
}

.start-options-button {
  margin: 20pt
}

.sentence {
  top: 40pt;
  left: 0;
  right: 0;
  bottom: 40pt;
  position: fixed;
  overflow-y: scroll;
}

.back-next-buttons{
  margin-top: 25pt;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.back-button {
  margin-right: 15pt;
}

.next-button {
  margin-left: 15pt;
}

.sentence-inside {
  min-height: 90%;
  padding-bottom: 30pt;
  padding-right: 10pt;
  padding-left: 10pt;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.response-grid-item {
  width: 100%;

}

.responseCard {
  width: 75%;
  padding: 10pt;
}

.prompt-text {
  width: 75%;
}

.flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.next-sentence {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 10pt;
}

.last-sentence {
  position: fixed;
  bottom: 0;
  left: 0;
  margin: 10pt;
}

.show-authors {
  background-color: white;
  position: fixed;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  left: 0;
  right: 0;
  top: 0;
  padding-top: 10pt;
}

.single-response {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10pt;
  max-width: 500pt;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
